<template>
<div id="signUp">
  <div class="container">
    <form>
    <div class="form-data">
      <div>
        <label for="account"></label>
        <input type="text" v-model="account" name="account" id="account" />
        <p>( 6-12_)</p>
      </div>
      <div>
        <label for="password"></label>
        <input type="password" v-model="password" name="password" id="password" />
        <p>( 8 1)</p>
      </div>
      <div>
        <label for="passwordAgain"></label>
        <input type="password" v-model="passwordAgain" name="passwordAgain" id="passwordAgain" />
        <p>{{passwordTest}}</p>
      </div>
      <div>
        <label for="name"></label>
        <input type="text" v-model="name" name="name" id="name" />
        <p></p>
      </div>
      <div>
        <label for="email"></label>
        <input type="text" v-model="email" name="email" id="email" />
        <p>( GmailEXyahoopchome)</p>
      </div>
    </div>
    <div class="data-submit">  
      <input type="button" @click="registorSubmit" value="" />
    </div>  
    </form>
  </div>
</div>
</template>

<script>
import {store} from '../store.js'
import {mapGetters,mapActions} from 'vuex'


export default{
    data(){
        return{
            account:'',
            password:'',
            passwordAgain:'',
            name:'',
            email:'',
            passwordTest:''
        }
    },
    methods:{
      ...mapActions([
        'registor'
      ]),
      checkData(dataArr){
        for(let i=0;i<dataArr.length;i++){
          if(dataArr[i] == ""){
            return "error";
          }
        }
      },
      async registorSubmit(){
          let name = store.methods.xssCheck(this.name);
          let account = store.methods.xssCheck(this.account);
          let password = store.methods.xssCheck(this.password);
          let email = store.methods.xssCheck(this.email);

          let checkResult = this.checkData([name,account,password,email]);

          if(checkResult == "error"){
            alert("");
            return;
          }
          let data = {
            name:name,
            account:account,
            password:password,
            email:email
          };
          await this.registor(data);
          this.$router.push({path:'/sign_up/signup_result'});
      }
    },
    watch:{
        passwordAgain:function(){
            if(this.password !== this.passwordAgain)
              this.passwordTest=''
            else
              this.passwordTest=''  
        }
    }
}
</script>

<style lang="scss">
#signUp .container{
  margin:20px 0;

  form{
    width:1000px;
    max-width:100%;
    margin:0 auto;

    .form-data{
      display:flex;
      flex-flow:column;
      //justify-content:center;
      margin:20px;
      border-bottom:1px solid blue;

      >div{
        width:100%;
        display:flex;
        //flex-flow:column;
        justify-content:center;
        align-items:center;
        margin:20px 0;

        label{
          width:20%;
          text-align:right;
          font-size:16px;
          margin-right:10px;
        }

        input{
          width:20%;
          max-width:150px;
          height:20px;
          margin-right:10px;
        }

        p{
          width:60%;
          font-size:13px;
          color:#9D9D9D;
        }

        &:nth-child(3) p{
          color:red;
        }
      }
    }

    .data-submit{
      display:flex;
      justify-content:center;
      margin:20px;

      input[type="button"]{
        width:120px;
        height:30px;
        text-align:center;
        line-height:30px;
        background:#FF8000;
        color:#fff;
        border:1px solid #FFBB77;
        border-radius:2px;
        cursor:pointer;

        &:hover{
          background:#FF9224;
        }
      }
    }
  }
}
@media(max-width:576px){
  #signUp .container{
    form{
      .form-data{
        align-items:center;

        >div{
          width:70%;
          flex-flow:column;
          align-items:flex-start;

          label{
            width:100%;
            text-align:left;
            margin-right:0;
            margin-bottom:10px;
          }

          input{
            width:100%;
            margin-right:0;
            margin-bottom:10px;
          }

          p{
            width:100%;
          }
        }
      }
    }
  }
}
</style>